<script>
import { defineComponent } from '@vue/composition-api'
import AmountView from '../components/AmountView.vue'
import DepositOptions from '../components/DepositOptions.vue'
import WithdrawOptions from '../components/WithdrawOptions.vue'
// import LiquidityBalanceDepositModal from '../components/newModals/LiquidityBalanceDepositModal.vue'
import LiquidityBalanceWithdrawModal from '../components/newModals/LiquidityBalanceWithdrawModal.vue'
import BonusWithdrawalConfirmationModal from "@/components/newModals/BonusWithdrawalConfirmationModal.vue"
// import LiquidityBalanceDepositFromExternalWallet from '../components/newModals/LiquidityBalanceDepositFromExternalWallet.vue'
// import WithdrawFromGoalModal from '../components/newModals/WithdrawFromGoalModal.vue'
// import Modal from '../components/modals/Modal.vue'
import Goal from '../components/Goal.vue'
import AddGoalModal from '../components/newModals/AddGoalModal.vue'
import LiquidityBalanceWithdrawToExternalWallet from '../components/newModals/LiquidityBalanceWithdrawToExternalWallet.vue'

import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    setup() {
        
    },
    components: {
        Goal,
        AmountView,
        DepositOptions,
        WithdrawOptions,
        // LiquidityBalanceDepositModal,
        LiquidityBalanceWithdrawModal,
        BonusWithdrawalConfirmationModal,
        // LiquidityBalanceDepositFromExternalWallet,
        // Modal,
        // WithdrawFromGoalModal,
        AddGoalModal,
        LiquidityBalanceWithdrawToExternalWallet
    },
    data: ()=> ({
        showDepositDropdown: false,
        showWithdrawDropdown: false,
        depositSource: null, // wallet, bonusAccount, externalWallet
        showDepositFromExternalWalletModal: false,
        withdrawToWallet: false,
        showWithdrawToExternalWalletModal: false,
        showWithdrawalConfirmationModal: false,
        showBalanceDepositModal: false,
        showBalanceWithdrawModal: false,
        interval: null,
        showAddGoalModal: false
        // currentGoal: null,
        // showWithdrawModal: false
    }),
    computed: {
        goalsData() {
            return this.$store.getters.goalsData
        },
        goals() {
            return this.goalsData?.goals || []
        },
        balance() {
            return this.goalsData?.balance || 0
        }
    },
    methods: {
        addGoalSuccesfull() {
            this.closeModals()
            this.reloadData()
        },
        // withdrawFromGoal(goal) {
        //     this.currentGoal = goal
        //     this.showWithdrawModal = true
        // },
        async withdrawFromGoal(goal) {
            const data = {goalId: goal.goalId}
            try {
                await this.$store.dispatch('liquidityProvidingWithdrawGoalProfit', data)
                this.$message(localizeFilter('withdrawing_funds_successful'))
                this.reloadData()
            }
            catch (e) {
                this.$error(e)
            } 
        },
        showDepositOptions() {
            this.showDepositDropdown = true
            this.showWithdrawDropdown = false
        },
        showWithdrawOptions() {
            this.showWithdrawDropdown = true
            this.showDepositDropdown = false
        },
        hideDropdowns() {
            this.showDepositDropdown = false
            this.showWithdrawDropdown = false
        }, 
        selectDepositOption(option) {
            if (option === 'from-wallet') {
                this.depositSource = 'wallet'
                this.showBalanceDepositModal = true
            } else if (option === 'from-bonus-account') {
                this.depositSource = 'bonusAccount'
                this.showBalanceDepositModal = true
            } else if (option === 'from-external-wallet') {
                this.depositSource = 'externalWallet'
                this.showDepositFromExternalWalletModal = true
            }
        },
        selectWithdrawOption(option) {
            if (option === 'to-wallet') {
                this.withdrawToWallet = true
                this.showBalanceWithdrawModal = true
            } else if (option === 'to-bonus-account') {
                this.withdrawToWallet = false
                this.showBalanceWithdrawModal = true
            } else if (option === 'to-external-wallet') {
                this.withdrawToWallet = false
                this.showWithdrawToExternalWalletModal = true
            }
        },
        showWithdrawalConfirmation(withdrawBalanceRequestId, fee) {
            this.closeModal()
            this.withdrawId = withdrawBalanceRequestId
            this.commission = fee || 0
            setTimeout(()=>{
                this.showWithdrawalConfirmationModal = true
            })
        },
        closeModal() {
            this.showWithdrawToExternalWalletModal = false
            // this.clearAll()
        },
        closeConfirmationModal() {
            this.showWithdrawalConfirmationModal = false
            this.closeModal()
        },
        closeModals() {
            this.showBalanceDepositModal = false
            this.showBalanceWithdrawModal = false
            this.depositSource = null
            this.withdrawToWallet = false
            this.showDepositFromExternalWalletModal = false
            this.showWithdrawToExternalWalletModal = false
            this.showAddGoalModal = false
            // this.currentGoal = null
            // this.showWithdrawModal = false
            // this.loadData()
        },
        async reloadData() {
            await this.loadData(true)
        },
        async loadData(silentCatch) {
            try {
                await this.$store.dispatch('getGoalsData')
            }
            catch (e) {
                if(!silentCatch) {
                    this.$error(e)
                }
            }  
        }
    },
    async mounted() {
        await this.loadData()
        this.interval = setInterval(async () => {
            await this.loadData()
        }, 3000)
    },
    unmounted() {
        clearInterval(this.interval)
        this.interval = null
    }  

})
</script>

